@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;padding:1em}.main{display:flex;height:90vh;align-items:center;justify-content:center}.main #drum-machine{background-color:#000;display:flex;flex-direction:row;width:550px;height:350px;padding:10px;grid-gap:10px;gap:10px;border-radius:8px}.main #drum-machine .pad-container{display:grid;grid-template-column:100px 100px 100px;grid-template-row:100px 100px 100px;grid-template-areas:"key-1 key-2 key-3" "key-4 key-5 key-6" "key-7 key-8 key-9";padding:10px;width:330px;height:auto;background-color:#fff;grid-gap:10px;gap:10px;border-radius:8px}.main #drum-machine .pad-container .drum-pad{display:flex;width:100px;height:100px;border:0;border-radius:8px;align-items:center;justify-content:center;font-size:24px;font-family:Rubik;box-shadow:0 0 1rem 0 rgba(0,0,0,.5);background-color:rgba(255,255,255,.1);-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);-webkit-user-select:none;-ms-user-select:none;user-select:none}.main #drum-machine .pad-container .drum-pad:hover{cursor:default}.main #drum-machine .pad-container .keydown{background-color:rgba(255,0,0,.7);box-shadow:0 0 2rem 0 rgba(255,0,0,.8)}.main #drum-machine .display-div{display:flex;align-items:center;justify-content:center;background-color:#fff;width:100%;height:100%;border-radius:8px}.main #drum-machine .display-div #display{display:inline-block;text-align:center;width:100%;font-size:20px;font-family:Rubik}
